import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home/index'
import Login from '@/views/Login/index'
import SignIn from '@/views/SignIn/index'
import SignOut from '@/views/SignOut/index'
import TimeOut from '@/views/TimeOut/index'
import Record2 from '@/views/Record2/index'
import Record3 from '@/views/Record3/index'
import Signature from '@/views/Signature/index'
// import Home from '@/views/Login/index'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/sign-in'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/sign-in',
      name: 'SignIn',
      component: SignIn
    },
    {
      path: '/sign-out',
      name: 'SignOut',
      component: SignOut
    },
    {
      path: '/record2',
      name: 'Record2',
      component: Record2
    },
    {
      path: '/record3',
      name: 'Record3',
      component: Record3
    },
    {
      path: '/time-out',
      name: 'TimeOut',
      component: TimeOut
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/signature',
      name: 'Signature',
      component: Signature
    }
  ]
})



